<template>
  <q-page padding class="container">
    <q-card flat bordered class="rounded card-padding-title">
      <q-toolbar class="q-pa-none no-min-height">
        <q-toolbar-title>
          <div class="font-medium text-h5 text-primary-700">{{ title }}</div>
        </q-toolbar-title>
      </q-toolbar>
    </q-card>

    <HelpCenter
      v-if="!external"
      :noclose="true"
      bgcolor="accent2"
      class="full-width"
      style="height: calc(100vh - 125px) !important"
    />

    <div v-else class="text-center full-width q-pt-xl">
      Connecting to help page...
    </div>
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import HelpCenter from 'src/components/HelpCenter.vue';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'HelpCenterPage',

  props: {
    external: {
      type: Boolean,
      default: true,
    },
  },

  components: { HelpCenter },

  data() {
    return {
      title: 'Help Center',
    };
  },

  computed: {
    ...mapState(useGrowmodo, ['externalLinks']),
  },

  watch: {
    externalLinks: {
      async handler(val) {
        if (this.external && val?.help_link)
          window.location.href = val?.help_link;
      },
    },
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
  },
});
</script>
